<template>
  <div class="quick-test section">
    <h3 class="section-title"></h3>
    <TestCase
      title="API "
      description=" Navigation API  History API "
      :actions="testActions"
      :result="testResult"
      :status="testStatus"
    />
  </div>
</template>

<script>
import TestCase from '@/components/common/TestCase.vue'

export default {
  name: 'QuickTest',
  components: {
    TestCase
  },
  data() {
    return {
      testResult: '',
      testStatus: '',
      testActions: [
        {
          name: 'navigation-api',
          label: ' Navigation API',
          handler: this.testNavigationAPI,
          type: 'btn-primary'
        },
        {
          name: 'history-api',
          label: ' History API',
          handler: this.testHistoryAPI,
          type: 'btn-secondary'
        }
      ]
    }
  },
  methods: {
    testNavigationAPI() {
      try {
        if ('navigation' in window) {
          this.testResult = `Navigation API 
 URL: ${location.href}
: ${navigation.entries().length}
: ${navigation.currentEntry?.index || 'N/A'}`
          this.testStatus = 'success'
        } else {
          this.testResult = 'Navigation API '
          this.testStatus = 'warning'
        }
      } catch (error) {
        this.testResult = `Navigation API : ${error.message}`
        this.testStatus = 'error'
      }
    },
    
    testHistoryAPI() {
      try {
        this.testResult = `History API :
 URL: ${location.href}
History : ${history.length}
State: ${JSON.stringify(history.state, null, 2) || 'null'}`
        this.testStatus = 'success'
      } catch (error) {
        this.testResult = `History API : ${error.message}`
        this.testStatus = 'error'
      }
    }
  }
}
</script>