export const entriesUtils = {
  //  Navigation API 
  getNavigationEntries() {
    try {
      if ('navigation' in window && navigation.entries) {
        const entries = navigation.entries()
        const currentEntry = navigation.currentEntry
        return {
          entries: entries.map((entry, index) => ({
            id: entry.id,
            key: entry.key,
            index: index,
            url: entry.url,
            state: entry.getState ? entry.getState() : null
          })),
          currentId: currentEntry ? currentEntry.id : null
        }
      }
    } catch (error) {
      console.warn('Navigation API entries not available:', error)
    }
    
    return {
      entries: [],
      currentId: null
    }
  },

  //  History API 
  getHistoryInfo() {
    return {
      length: history.length,
      state: history.state,
      url: location.href
    }
  },

  //  Navigation API 
  addNavigationEntry() {
    try {
      const newUrl = `${location.pathname}?nav_test=${Date.now()}`
      
      if ('navigation' in window && navigation.navigate) {
        navigation.navigate(newUrl, {
          state: { timestamp: Date.now(), type: 'navigation_test' }
        })
      } else {
        //  History API
        history.pushState(
          { timestamp: Date.now(), type: 'navigation_test' },
          '',
          newUrl
        )
      }
    } catch (error) {
      console.error('Failed to add navigation entry:', error)
    }
  },

  //  History API 
  addHistoryEntry() {
    try {
      const newUrl = `${location.pathname}?history_test=${Date.now()}`
      history.pushState(
        { timestamp: Date.now(), type: 'history_test' },
        '',
        newUrl
      )
    } catch (error) {
      console.error('Failed to add history entry:', error)
    }
  },

  // 
  navigateToEntry(entry) {
    try {
      if ('navigation' in window && navigation.traverseTo) {
        navigation.traverseTo(entry.key)
        return { success: true, message: `: ${entry.url}` }
      } else {
        return { success: false, message: 'Navigation API  traverseTo ' }
      }
    } catch (error) {
      return { success: false, message: `: ${error.message}` }
    }
  },

  // 
  testTraversal() {
    try {
      if ('navigation' in window && navigation.entries) {
        const entries = navigation.entries()
        return {
          success: true,
          message: ` ${entries.length} `
        }
      } else {
        //  History API 
        return {
          success: true,
          message: `History API  ${history.length}`
        }
      }
    } catch (error) {
      return {
        success: false,
        message: `: ${error.message}`
      }
    }
  },

  // 
  testCurrentEntry() {
    try {
      if ('navigation' in window && navigation.currentEntry) {
        const current = navigation.currentEntry
        return {
          success: true,
          message: `: ${current.url} (: ${current.index})`
        }
      } else {
        return {
          success: true,
          message: ` URL: ${location.href} (History : ${history.length})`
        }
      }
    } catch (error) {
      return {
        success: false,
        message: `: ${error.message}`
      }
    }
  },

  // 
  testEntryUpdate() {
    try {
      if ('navigation' in window && navigation.updateCurrentEntry) {
        navigation.updateCurrentEntry({ 
          state: { updated: Date.now(), test: true }
        })
        return {
          success: true,
          message: ''
        }
      } else {
        history.replaceState(
          { updated: Date.now(), test: true },
          '',
          location.href
        )
        return {
          success: true,
          message: ' History API '
        }
      }
    } catch (error) {
      return {
        success: false,
        message: `: ${error.message}`
      }
    }
  }
}