// 
export const browserDetectionUtils = {
  detectBrowser() {
    const userAgent = navigator.userAgent
    let browserName = 'Unknown'
    let browserVersion = 'Unknown'
    let browserEngine = 'Unknown'
    
    // Chrome
    if (userAgent.includes('Chrome') && !userAgent.includes('Edg')) {
      browserName = 'Chrome'
      const match = userAgent.match(/Chrome\/(\d+)/)
      if (match) browserVersion = match[1]
      browserEngine = 'Blink'
    }
    // Firefox
    else if (userAgent.includes('Firefox')) {
      browserName = 'Firefox'
      const match = userAgent.match(/Firefox\/(\d+)/)
      if (match) browserVersion = match[1]
      browserEngine = 'Gecko'
    }
    // Safari
    else if (userAgent.includes('Safari') && !userAgent.includes('Chrome')) {
      browserName = 'Safari'
      const match = userAgent.match(/Version\/(\d+)/)
      if (match) browserVersion = match[1]
      browserEngine = 'WebKit'
    }
    // Edge
    else if (userAgent.includes('Edg')) {
      browserName = 'Edge'
      const match = userAgent.match(/Edg\/(\d+)/)
      if (match) browserVersion = match[1]
      browserEngine = 'Blink'
    }
    
    return {
      name: browserName,
      version: browserVersion,
      engine: browserEngine,
      platform: navigator.platform,
      userAgent: userAgent
    }
  },

  // 
  getBrowserInfo() {
    return this.detectBrowser()
  },

  detectNavigationSupport() {
    const hasNavigation = 'navigation' in window
    const hasNavigate = hasNavigation && typeof navigation.navigate === 'function'
    const hasEntries = hasNavigation && typeof navigation.entries === 'function'
    const hasCurrentEntry = hasNavigation && 'currentEntry' in navigation
    
    // 
    let hasNavigateEvent = false
    if (hasNavigation) {
      try {
        const testListener = () => {}
        navigation.addEventListener('navigate', testListener)
        navigation.removeEventListener('navigate', testListener)
        hasNavigateEvent = true
      } catch (error) {
        hasNavigateEvent = false
      }
    }

    return {
      hasNavigation,
      hasNavigate,
      hasEntries,
      hasCurrentEntry,
      hasNavigateEvent
    }
  },
  
  testNavigationApiSupport() {
    const features = []
    
    // 
    features.push({
      name: 'navigation ',
      supported: 'navigation' in window,
      description: ' navigation '
    })
    
    if ('navigation' in window) {
      // 
      features.push({
        name: 'currentEntry',
        supported: 'currentEntry' in navigation,
        description: ''
      })
      
      // 
      const methods = ['navigate', 'back', 'forward', 'traverseTo', 'updateCurrentEntry', 'reload', 'entries']
      methods.forEach(method => {
        features.push({
          name: `${method}()`,
          supported: typeof navigation[method] === 'function',
          description: `${method} `
        })
      })
      
      // 
      try {
        const testListener = () => {}
        navigation.addEventListener('navigate', testListener)
        navigation.removeEventListener('navigate', testListener)
        features.push({
          name: 'navigate ',
          supported: true,
          description: ''
        })
      } catch (error) {
        features.push({
          name: 'navigate ',
          supported: false,
          description: ''
        })
      }
    } else {
      // Navigation API 
      const unsupportedFeatures = [
        'currentEntry', 'navigate()', 'back()', 'forward()', 
        'traverseTo()', 'updateCurrentEntry()', 'reload()', 
        'entries()', 'navigate '
      ]
      
      unsupportedFeatures.forEach(feature => {
        features.push({
          name: feature,
          supported: false,
          description: `${feature}  - Navigation API `
        })
      })
    }
    
    return features
  }
}

export const compatibilityTestUtils = {
  runTests() {
    const results = []
    
    // 
    results.push({
      test: 'Navigation API ',
      status: 'navigation' in window ? '' : '',
      description: 'navigation' in window ? 
        'Navigation API ' : 
        'Navigation API  Polyfill ',
      details: `: ${'navigation' in window ? '' : ''}`
    })
    
    // 
    if ('navigation' in window) {
      try {
        const testListener = () => {}
        navigation.addEventListener('navigate', testListener)
        navigation.removeEventListener('navigate', testListener)
        results.push({
          test: '',
          status: '',
          description: 'navigate ',
          details: ''
        })
      } catch (error) {
        results.push({
          test: '',
          status: '',
          description: 'navigate ',
          details: `: ${error.message}`
        })
      }
      
      // 
      const methods = ['navigate', 'back', 'forward', 'entries']
      methods.forEach(method => {
        const available = typeof navigation[method] === 'function'
        results.push({
          test: `${method} `,
          status: available ? '' : '',
          description: available ? 
            `${method} ` : 
            `${method} `,
          details: `: ${typeof navigation[method]}`
        })
      })
      
      // 
      try {
        const currentEntry = navigation.currentEntry
        results.push({
          test: 'currentEntry ',
          status: currentEntry !== undefined ? '' : '',
          description: currentEntry !== undefined ? 
            'currentEntry ' : 
            'currentEntry  undefined',
          details: `: ${currentEntry ? '' : ''}`
        })
      } catch (error) {
        results.push({
          test: 'currentEntry ',
          status: '',
          description: 'currentEntry ',
          details: `: ${error.message}`
        })
      }
    } else {
      results.push({
        test: 'History API ',
        status: 'history' in window ? '' : '',
        description: 'history' in window ? 
          'History API ' : 
          'History API ',
        details: `History API : ${'history' in window ? '' : ''}`
      })
    }
    
    return results
  }
}