export const polyfillTestUtils = {
  loadPolyfill() {
    return `Polyfill 
-  navigation 
-  NavigateEvent 
- 
-  History API 
- : 


 window.navigation 
 NavigateEvent 
 
 History API `
  },
  
  testComplete() {
    const testResults = []
    
    // 
    if (typeof window.navigation !== 'undefined') {
      testResults.push(' navigation ')
    } else {
      testResults.push(' navigation ')
    }
    
    // 
    if (window.navigation && typeof window.navigation.addEventListener === 'function') {
      testResults.push(' ')
    } else {
      testResults.push(' ')
    }
    
    // 
    if (window.navigation && typeof window.navigation.navigate === 'function') {
      testResults.push(' navigate ')
    } else {
      testResults.push(' navigate ')
    }
    
    // 
    if (window.navigation && typeof window.navigation.entries === 'function') {
      testResults.push(' entries ')
    } else {
      testResults.push(' entries ')
    }
    
    // 
    if (window.navigation && window.navigation.currentEntry) {
      testResults.push(' currentEntry ')
    } else {
      testResults.push(' currentEntry ')
    }
    
    const successCount = testResults.filter(result => result.startsWith('')).length
    const totalCount = testResults.length
    
    return ` Polyfill 
${testResults.join('\n')}

: ${successCount}/${totalCount} (${Math.round(successCount/totalCount*100)}%)


- : ${successCount >= 3 ? '' : ''}
- : ${successCount === totalCount ? '' : ''}
- : ${successCount < totalCount ? '' : ''}`
  }
}