export const eventTestUtils = {
  testNavigateEvent() {
    try {
      if ('navigation' in window && navigation.navigate) {
        const testUrl = `${location.pathname}?navigate_test=${Date.now()}`
        navigation.navigate(testUrl, {
          state: { test: 'navigate_event', timestamp: Date.now() }
        })
        return {
          success: true,
          message: `Navigation API \nURL: ${testUrl}\n:  navigate `
        }
      } else {
        return {
          success: false,
          message: 'Navigation API \n navigate '
        }
      }
    } catch (error) {
      return {
        success: false,
        message: `Navigation API : ${error.message}`
      }
    }
  },

  testInterceptNavigation() {
    try {
      if ('navigation' in window) {
        // 
        const interceptor = (event) => {
          event.intercept({
            handler: () => {
              return new Promise((resolve) => {
                setTimeout(() => {
                  console.log('')
                  resolve()
                }, 1000)
              })
            }
          })
        }
        
        navigation.addEventListener('navigate', interceptor)
        
        // 
        const testUrl = `${location.pathname}?intercept_test=${Date.now()}`
        navigation.navigate(testUrl)
        
        // 
        setTimeout(() => {
          navigation.removeEventListener('navigate', interceptor)
        }, 2000)
        
        return {
          success: true,
          message: `\n1\nURL: ${testUrl}`
        }
      } else {
        return {
          success: false,
          message: 'Navigation API \n'
        }
      }
    } catch (error) {
      return {
        success: false,
        message: `: ${error.message}`
      }
    }
  },

  testCancelNavigation() {
    try {
      if ('navigation' in window) {
        // 
        const cancelInterceptor = (event) => {
          event.preventDefault()
          console.log('')
        }
        
        navigation.addEventListener('navigate', cancelInterceptor)
        
        // 
        const testUrl = `${location.pathname}?cancel_test=${Date.now()}`
        navigation.navigate(testUrl)
        
        // 
        setTimeout(() => {
          navigation.removeEventListener('navigate', cancelInterceptor)
        }, 1000)
        
        return {
          success: true,
          message: `\n\nURL: ${testUrl}`
        }
      } else {
        return {
          success: false,
          message: 'Navigation API \n'
        }
      }
    } catch (error) {
      return {
        success: false,
        message: `: ${error.message}`
      }
    }
  },

  testPopstateEvent() {
    try {
      //  popstate 
      const popstateHandler = (event) => {
        console.log('Popstate event triggered:', event.state)
      }
      
      window.addEventListener('popstate', popstateHandler)
      
      // 
      const testState = { test: 'popstate', timestamp: Date.now() }
      const testUrl = `${location.pathname}?popstate_test=${Date.now()}`
      history.pushState(testState, '', testUrl)
      
      //  popstate
      setTimeout(() => {
        history.back()
        setTimeout(() => {
          window.removeEventListener('popstate', popstateHandler)
        }, 500)
      }, 100)
      
      return {
        success: true,
        message: `Popstate \n\n: ${JSON.stringify(testState)}`
      }
    } catch (error) {
      return {
        success: false,
        message: `Popstate : ${error.message}`
      }
    }
  },

  testHistoryChange() {
    try {
      const originalUrl = location.href
      const testUrl = `${location.pathname}?history_change=${Date.now()}`
      const testState = { test: 'history_change', timestamp: Date.now() }
      
      //  pushState 
      history.pushState(testState, '', testUrl)
      
      return {
        success: true,
        message: `\nURL: ${originalUrl}\nURL: ${testUrl}\n: ${JSON.stringify(testState)}`
      }
    } catch (error) {
      return {
        success: false,
        message: `: ${error.message}`
      }
    }
  },

  testBeforeUnload() {
    try {
      //  beforeunload 
      const beforeUnloadHandler = (event) => {
        const message = ''
        event.returnValue = message
        return message
      }
      
      window.addEventListener('beforeunload', beforeUnloadHandler)
      
      // 5
      setTimeout(() => {
        window.removeEventListener('beforeunload', beforeUnloadHandler)
      }, 5000)
      
      return {
        success: true,
        message: `BeforeUnload \n\n5\n`
      }
    } catch (error) {
      return {
        success: false,
        message: `BeforeUnload : ${error.message}`
      }
    }
  }
}