// 
export const performanceTestUtils = {
  // 
  async testBasicNavigation() {
    const startTime = performance.now()
    
    try {
      // 
      for (let i = 0; i < 100; i++) {
        if (window.navigation) {
          //  Navigation API
          const url = `${window.location.origin}${window.location.pathname}?test=${i}`
          await window.navigation.navigate(url, { history: 'replace' }).finished
        } else {
          //  History API
          const url = `${window.location.origin}${window.location.pathname}?test=${i}`
          window.history.replaceState({ test: i }, '', url)
        }
      }
      
      const endTime = performance.now()
      const duration = Math.round(endTime - startTime)
      
      return {
        duration,
        details: `100${(duration/100).toFixed(2)}ms`
      }
    } catch (error) {
      const endTime = performance.now()
      return {
        duration: Math.round(endTime - startTime),
        details: `: ${error.message}`
      }
    }
  },

  // 
  async testStateManagement() {
    const startTime = performance.now()
    
    try {
      const states = []
      
      // 
      for (let i = 0; i < 1000; i++) {
        const state = {
          id: i,
          data: `test-data-${i}`,
          timestamp: Date.now(),
          nested: {
            value: Math.random(),
            array: new Array(10).fill(i)
          }
        }
        states.push(state)
        
        if (window.navigation) {
          const url = `${window.location.origin}${window.location.pathname}?state=${i}`
          await window.navigation.navigate(url, { 
            state,
            history: 'replace'
          }).finished
        } else {
          const url = `${window.location.origin}${window.location.pathname}?state=${i}`
          window.history.replaceState(state, '', url)
        }
      }
      
      const endTime = performance.now()
      const duration = Math.round(endTime - startTime)
      
      return {
        duration,
        details: `1000${(duration/1000).toFixed(2)}ms`
      }
    } catch (error) {
      const endTime = performance.now()
      return {
        duration: Math.round(endTime - startTime),
        details: `: ${error.message}`
      }
    }
  },

  // 
  async testEventHandling() {
    const startTime = performance.now()
    
    try {
      let eventCount = 0
      const events = []
      
      // 
      const handleNavigate = (event) => {
        eventCount++
        events.push({
          type: event.type,
          timestamp: Date.now(),
          url: event.destination?.url
        })
      }
      
      if (window.navigation) {
        window.navigation.addEventListener('navigate', handleNavigate)
        
        // 
        for (let i = 0; i < 50; i++) {
          const url = `${window.location.origin}${window.location.pathname}?event=${i}`
          await window.navigation.navigate(url, { history: 'replace' }).finished
        }
        
        window.navigation.removeEventListener('navigate', handleNavigate)
      } else {
        // 
        for (let i = 0; i < 50; i++) {
          handleNavigate({
            type: 'navigate',
            destination: { url: `${window.location.origin}${window.location.pathname}?event=${i}` }
          })
        }
      }
      
      const endTime = performance.now()
      const duration = Math.round(endTime - startTime)
      
      return {
        duration,
        details: `${eventCount}${(duration/eventCount).toFixed(2)}ms`
      }
    } catch (error) {
      const endTime = performance.now()
      return {
        duration: Math.round(endTime - startTime),
        details: `: ${error.message}`
      }
    }
  },

  // 
  async preloadRoutes() {
    const startTime = performance.now()
    
    try {
      const routes = [
        '/home',
        '/basic-navigation',
        '/navigation-entries',
        '/navigation-events',
        '/polyfill-analysis',
        '/browser-support'
      ]
      
      const preloadPromises = routes.map(async (route) => {
        // 
        return new Promise(resolve => {
          setTimeout(() => {
            resolve({
              route,
              loaded: true,
              timestamp: Date.now()
            })
          }, Math.random() * 100)
        })
      })
      
      const results = await Promise.all(preloadPromises)
      
      const endTime = performance.now()
      const duration = Math.round(endTime - startTime)
      
      return {
        duration,
        details: `${results.length}${results.filter(r => r.loaded).length}`
      }
    } catch (error) {
      const endTime = performance.now()
      return {
        duration: Math.round(endTime - startTime),
        details: `: ${error.message}`
      }
    }
  },

  // 
  async clearCache() {
    const startTime = performance.now()
    
    try {
      // 
      const cache = new Map()
      
      // 
      for (let i = 0; i < 10000; i++) {
        cache.set(`key-${i}`, {
          data: `value-${i}`,
          timestamp: Date.now(),
          size: Math.random() * 1000
        })
      }
      
      // 
      const initialSize = cache.size
      cache.clear()
      
      // 
      if (window.gc) {
        window.gc()
      }
      
      const endTime = performance.now()
      const duration = Math.round(endTime - startTime)
      
      return {
        duration,
        details: `${initialSize}`
      }
    } catch (error) {
      const endTime = performance.now()
      return {
        duration: Math.round(endTime - startTime),
        details: `: ${error.message}`
      }
    }
  }
}