import { createApp } from 'vue'
import { createRouter, createWebHistory } from 'vue-router'
import App from './App.vue'

// 
import Home from './pages/Home/index.vue'
import BasicNavigation from './pages/BasicNavigation/index.vue'
import AdvancedScenarios from './pages/AdvancedScenarios/index.vue'
import NavigationEntries from './pages/NavigationEntries/index.vue'
import NavigationEvents from './pages/NavigationEvents/index.vue'
import BrowserSupport from './pages/BrowserSupport/index.vue'
import PolyfillAnalysis from './pages/PolyfillAnalysis/index.vue'

//  base 
const basePath = import.meta.env.VITE_BASE_PATH || '/'

// 
const routes = [
  { path: '/', component: Home, meta: { title: 'Navigation API Demo - ' } },
  { path: '/basic-navigation', component: BasicNavigation, meta: { title: 'Navigation API Demo - ' } },
  { path: '/advanced-scenarios', component: AdvancedScenarios, meta: { title: 'Navigation API Demo - ' } },
  { path: '/navigation-entries', component: NavigationEntries, meta: { title: 'Navigation API Demo - ' } },
  { path: '/navigation-events', component: NavigationEvents, meta: { title: 'Navigation API Demo - ' } },
  { path: '/polyfill-analysis', component: PolyfillAnalysis, meta: { title: 'Navigation API Demo - Polyfill ' } },
  { path: '/browser-support', component: BrowserSupport, meta: { title: 'Navigation API Demo - ' } }
]

const router = createRouter({
  history: createWebHistory(basePath),
  routes
})

//  - 
router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
})

const app = createApp(App)
app.use(router)
app.mount('#app')