// 
export const historyManagementUtils = {
  // 
  loadHistoryEntries() {
    const entries = []
    
    if (window.navigation && window.navigation.entries) {
      //  Navigation API 
      window.navigation.entries().forEach((entry, index) => {
        entries.push({
          id: entry.id || `entry-${index}`,
          url: entry.url,
          key: entry.key,
          state: entry.getState ? entry.getState() : null,
          index: index,
          sameDocument: entry.sameDocument,
          timestamp: Date.now() - (entries.length - index) * 1000
        })
      })
    } else {
      // 
      const currentUrl = window.location.href
      entries.push({
        id: 'current',
        url: currentUrl,
        key: 'current-key',
        state: window.history.state,
        index: 0,
        sameDocument: true,
        timestamp: Date.now()
      })
    }
    
    return entries
  },

  // 
  async addHistoryEntry(url, state = null, title = '') {
    try {
      if (window.navigation) {
        //  Navigation API
        const result = await window.navigation.navigate(url, {
          state,
          history: 'push'
        })
        
        if (title) {
          document.title = title
        }
        
        return {
          success: true,
          entry: {
            url,
            state,
            title,
            timestamp: Date.now()
          }
        }
      } else {
        //  History API
        window.history.pushState(state, title, url)
        
        if (title) {
          document.title = title
        }
        
        return {
          success: true,
          entry: {
            url,
            state,
            title,
            timestamp: Date.now()
          }
        }
      }
    } catch (error) {
      return {
        success: false,
        error: error.message
      }
    }
  },

  // 
  async replaceHistoryEntry(url, state = null, title = '') {
    try {
      if (window.navigation) {
        //  Navigation API
        const result = await window.navigation.navigate(url, {
          state,
          history: 'replace'
        })
        
        if (title) {
          document.title = title
        }
        
        return {
          success: true,
          entry: {
            url,
            state,
            title,
            timestamp: Date.now()
          }
        }
      } else {
        //  History API
        window.history.replaceState(state, title, url)
        
        if (title) {
          document.title = title
        }
        
        return {
          success: true,
          entry: {
            url,
            state,
            title,
            timestamp: Date.now()
          }
        }
      }
    } catch (error) {
      return {
        success: false,
        error: error.message
      }
    }
  },

  // 
  async navigateToEntry(entry) {
    try {
      if (window.navigation && entry.key) {
        //  Navigation API  traverseTo 
        if (window.navigation.traverseTo) {
          await window.navigation.traverseTo(entry.key)
        } else {
          //  traverseTo navigate
          await window.navigation.navigate(entry.url)
        }
      } else {
        //  History API
        const currentIndex = window.history.length - 1
        const targetIndex = entry.index || 0
        const delta = targetIndex - currentIndex
        
        if (delta !== 0) {
          window.history.go(delta)
        }
      }
      
      return { success: true }
    } catch (error) {
      return {
        success: false,
        error: error.message
      }
    }
  },

  // 
  clearHistory() {
    try {
      // 
      // 
      const currentUrl = window.location.href
      const currentState = window.history.state
      
      if (window.navigation) {
        //  Navigation API 
        window.navigation.navigate(currentUrl, {
          state: null,
          history: 'replace'
        })
      } else {
        //  History API 
        window.history.replaceState(null, '', currentUrl)
      }
      
      return {
        success: true,
        message: ''
      }
    } catch (error) {
      return {
        success: false,
        error: error.message
      }
    }
  },

  // 
  exportHistory() {
    try {
      const entries = this.loadHistoryEntries()
      const exportData = {
        timestamp: new Date().toISOString(),
        userAgent: navigator.userAgent,
        currentUrl: window.location.href,
        entries: entries.map(entry => ({
          url: entry.url,
          state: entry.state,
          timestamp: entry.timestamp,
          index: entry.index
        }))
      }
      
      const dataStr = JSON.stringify(exportData, null, 2)
      const dataBlob = new Blob([dataStr], { type: 'application/json' })
      const url = URL.createObjectURL(dataBlob)
      
      const link = document.createElement('a')
      link.href = url
      link.download = `navigation-history-${Date.now()}.json`
      document.body.appendChild(link)
      link.click()
      document.body.removeChild(link)
      URL.revokeObjectURL(url)
      
      return {
        success: true,
        message: ''
      }
    } catch (error) {
      return {
        success: false,
        error: error.message
      }
    }
  },

  // 
  removeEntry(entryId) {
    try {
      // 
      // 
      return {
        success: true,
        message: ` ${entryId}`
      }
    } catch (error) {
      return {
        success: false,
        error: error.message
      }
    }
  }
}