<template>
  <section class="section">
    <h2 class="section-title"></h2>
    <div class="form-demo">
      <div class="demo-description">
        <p></p>
      </div>
      
      <form @input="handleFormInput" class="demo-form">
        <div class="form-group">
          <label></label>
          <input v-model="formData.username" type="text" placeholder="">
        </div>
        <div class="form-group">
          <label></label>
          <input v-model="formData.email" type="email" placeholder="">
        </div>
        <div class="form-group">
          <label></label>
          <textarea v-model="formData.description" placeholder=""></textarea>
        </div>
        <div class="form-actions">
          <button type="button" @click="saveForm" class="btn btn-success"></button>
          <button type="button" @click="clearForm" class="btn btn-secondary"></button>
        </div>
      </form>
      
      <div class="form-status">
        <p>{{ formDirty ? '' : '' }}</p>
        <p>{{ navigationBlocked ? '' : '' }}</p>
      </div>
    </div>
  </section>
</template>

<script>
import { formInterceptionUtils } from '../utils/formInterceptionUtils.js'

export default {
  name: 'FormNavigationInterception',
  data() {
    return {
      formData: {
        username: '',
        email: '',
        description: ''
      },
      formDirty: false,
      navigationBlocked: false
    }
  },
  mounted() {
    formInterceptionUtils.setupNavigationInterception()
  },
  beforeUnmount() {
    formInterceptionUtils.cleanupNavigationInterception()
  },
  methods: {
    handleFormInput() {
      this.formDirty = true
      this.enableNavigationBlocking()
    },
    
    saveForm() {
      setTimeout(() => {
        this.formDirty = false
        this.disableNavigationBlocking()
        alert('')
      }, 500)
    },
    
    clearForm() {
      this.formData = {
        username: '',
        email: '',
        description: ''
      }
      this.formDirty = false
      this.disableNavigationBlocking()
    },
    
    enableNavigationBlocking() {
      if (this.navigationBlocked) return
      
      this.navigationBlocked = true
      formInterceptionUtils.enableBlocking(() => this.formDirty)
    },
    
    disableNavigationBlocking() {
      this.navigationBlocked = false
      formInterceptionUtils.disableBlocking()
    }
  }
}
</script>

