<template>
  <div class="navigation-comparison section">
    <h2 class="section-title"></h2>
    
    <div class="api-comparison">
      <div class="api-section">
        <h3>Navigation API</h3>
        <TestCase
          title="Navigation API "
          description=" Navigation API "
          :actions="navigationApiActions"
          :result="navigationApiResult"
        />
      </div>
      
      <div class="api-section">
        <h3>History API</h3>
        <TestCase
          title="History API "
          description=" History API "
          :actions="historyApiActions"
          :result="historyApiResult"
        />
      </div>
    </div>
    
    <ComparisonTable :headers="comparisonHeaders" :data="comparisonData" />
  </div>
</template>

<script>
import TestCase from '@/components/common/TestCase.vue'
import ComparisonTable from '@/components/common/ComparisonTable.vue'

export default {
  name: 'NavigationComparison',
  components: {
    TestCase,
    ComparisonTable
  },
  data() {
    return {
      navigationApiResult: '',
      historyApiResult: '',
      comparisonHeaders: ['', 'Navigation API', 'History API', ''],
      comparisonData: [
        ['', 'navigation.forward()', 'history.forward()', 'Navigation API  Promise'],
        ['', 'navigation.back()', 'history.back()', 'Navigation API  Promise'],
        ['', 'navigation.navigate(url)', 'history.pushState()', 'Navigation API '],
        ['', 'navigation.navigate(url, {history: "replace"})', 'history.replaceState()', '']
      ],
      navigationApiActions: [
        {
          name: 'navigate',
          label: '',
          handler: this.testNavigationApiNavigate
        },
        {
          name: 'back',
          label: '',
          handler: this.testNavigationApiBack
        },
        {
          name: 'forward',
          label: '',
          handler: this.testNavigationApiForward
        }
      ],
      historyApiActions: [
        {
          name: 'push',
          label: '',
          handler: this.testHistoryApiPush
        },
        {
          name: 'back',
          label: '',
          handler: this.testHistoryApiBack
        },
        {
          name: 'forward',
          label: '',
          handler: this.testHistoryApiForward
        }
      ]
    }
  },
  methods: {
    async testNavigationApiNavigate() {
      if ('navigation' in window) {
        try {
          const result = await navigation.navigate('?test=navigation-api&time=' + Date.now())
          this.navigationApiResult = `: ${result.committed ? '' : ''}`
        } catch (error) {
          this.navigationApiResult = `: ${error.message}`
        }
      } else {
        this.navigationApiResult = 'Navigation API '
      }
    },
    
    async testNavigationApiBack() {
      if ('navigation' in window) {
        try {
          const result = await navigation.back()
          this.navigationApiResult = `: ${result ? '' : ''}`
        } catch (error) {
          this.navigationApiResult = `: ${error.message}`
        }
      } else {
        this.navigationApiResult = 'Navigation API '
      }
    },
    
    async testNavigationApiForward() {
      if ('navigation' in window) {
        try {
          const result = await navigation.forward()
          this.navigationApiResult = `: ${result ? '' : ''}`
        } catch (error) {
          this.navigationApiResult = `: ${error.message}`
        }
      } else {
        this.navigationApiResult = 'Navigation API '
      }
    },
    
    testHistoryApiPush() {
      try {
        history.pushState({ test: 'history-api', time: Date.now() }, '', '?test=history-api&time=' + Date.now())
        this.historyApiResult = `: ${location.href}`
      } catch (error) {
        this.historyApiResult = `: ${error.message}`
      }
    },
    
    testHistoryApiBack() {
      try {
        history.back()
        this.historyApiResult = ''
      } catch (error) {
        this.historyApiResult = `: ${error.message}`
      }
    },
    
    testHistoryApiForward() {
      try {
        history.forward()
        this.historyApiResult = ''
      } catch (error) {
        this.historyApiResult = `: ${error.message}`
      }
    }
  }
}
</script>

<style scoped>
.api-comparison {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  margin-bottom: 2rem;
}

.api-section h3 {
  color: #495057;
  margin-bottom: 1rem;
  text-align: center;
}

@media (max-width: 768px) {
  .api-comparison {
    grid-template-columns: 1fr;
  }
}
</style>